$primaryColor: #4527a4;
$secondaryColor: #8344c5;

// Chart
.wp-block-b-blocks-chart {
	* {
		box-sizing: border-box;
	}

	.bBlocksChart {
		max-width: 100%;
		display: inline-block;

		canvas {
			padding: 10px 15px;
		}
	}

	.downloadArea {
		max-width: 100%;
		position: relative;
		display: inline-block;
		padding: 10px 15px;

		.downloadChart {
			display: inline-block;
			position: absolute;
			left: 0;
			top: 0;
			font-size: 16px;
			font-weight: bold;
			line-height: 100%;
			color: #fff;
			background-color: $primaryColor;
			padding: 8px 15px;
			border-radius: 5px;
			cursor: pointer;

			&:hover {
				background-color: $secondaryColor;
			}
		}
	}
}